.fec-bg-primary {
  background-color: var(--fec-bg-primary, #f6f6f3);
}

.fec-bg-secondary {
  background-color: var(--fec-bg-secondary, #ecece7);
}

.fec-bg-active {
  background-color: var(--fec-bg-active, #cfd8e3);
}

.fec-btn:hover {
  background-color: var(--fec-bg-active, #cfd8e3);
}

.fec-bg-panel {
  background-color: var(--fec-bg-panel, #ffffff);
}

.fec-color-primary {
  color: var(--fec-text-primary, #1f1f1f);
}

.fec-color-secondary {
  color: var(--fec-text-secondary, #5a5a5a);
}

.fec-bg-confirm {
  background-color: var(--fec-confirm, #4caf50);
}

.fec-color-confirm {
  color: var(--fec-confirm, #4caf50);
}

.fec-bg-danger {
  background-color: var(--fec-danger, #d9534f);
}

.fec-color-danger {
  color: var(--fec-danger, #d9534f);
}

.fec-border {
  border: var(--fec-border-width, 1px) var(--fec-border-style, solid)
    var(--fec-border-color, #d6d6d0);
}

.fec-shadow-soft {
  box-shadow: var(
    --fec-shadow-soft,
    0 1px 2px rgba(0, 0, 0, 0.05),
    0 3px 6px rgba(0, 0, 0, 0.06)
  );
}

.fec-shadow-medium {
  box-shadow: var(
    --fec-shadow-medium,
    0 4px 8px rgba(0, 0, 0, 0.08),
    0 8px 16px rgba(0, 0, 0, 0.06)
  );
}

.fec-opacity-80 {
  opacity: 0.8;
}

.fec-opacity-0 {
  opacity: 0;
}

.fec-z-top {
  z-index: 1100;
}

.fec-opacity-ease-out {
  transition: opacity 0.15s ease-out;
}

.fec-w-200 {
  width: 200px;
}

.fec-toast {
  width: 200px;
  min-height: 50px;
}

.fec-overlay {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  width: 100%;
  height: 100%;
}

.fec-toast-container {
  position: fixed;
}

.fec-toast-pos-br {
  bottom: var(--fec-toast-inset-y, 3rem);
  right: var(--fec-toast-inset-x, 1rem);
  top: auto;
  left: auto;
  align-items: flex-end;
}

.fec-toast-pos-bl {
  bottom: var(--fec-toast-inset-y, 3rem);
  left: var(--fec-toast-inset-x, 1rem);
  top: auto;
  right: auto;
  align-items: flex-start;
}

.fec-toast-pos-tr {
  top: var(--fec-toast-inset-y, 3rem);
  right: var(--fec-toast-inset-x, 1rem);
  bottom: auto;
  left: auto;
  align-items: flex-end;
}

.fec-toast-pos-tl {
  top: var(--fec-toast-inset-y, 3rem);
  left: var(--fec-toast-inset-x, 1rem);
  bottom: auto;
  right: auto;
  align-items: flex-start;
}

.fec-confirm-dialog {
  width: var(--fec-dialog-w, clamp(300px, 60vw, 400px));
  height: var(--fec-dialog-h, clamp(200px, 60vh, 300px));
  max-width: 90vw;
  max-height: 90vh;
}

.fec-confirm-dialog p {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.fec-modal {
  width: var(--fec-modal-w, clamp(320px, 80vw, 1536px));
  height: var(--fec-modal-h, clamp(240px, 80vh, 864px));
  max-width: 90vw;
  max-height: 90vh;
}

.fec-modal-body {
  flex: 1 1 auto;
  min-height: 0;
  min-width: 0;
  width: 100%;
}

.fec-modal-body-inner {
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
  box-sizing: border-box;
}
